@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?adi7xk');
  src:  url('../fonts/icomoon.eot?adi7xk#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?adi7xk') format('truetype'),
    url('../fonts/icomoon.woff?adi7xk') format('woff'),
    url('../fonts/icomoon.svg?adi7xk#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-times-solid:before {
  content: "\e900";
}
.icon-plus-solid:before {
  content: "\e901";
}
.icon-check-solid:before {
  content: "\e902";
}
.icon-list:before {
  content: "\e9ba";
}
